import React, { Component }  from 'react';
import '../Css/about.css';

export default class Users extends Component { 
  state = {
    output: 'List of Users',
        Users : [
          { 
            name: 'Bella',
            surname: 'Tau',
            age: 32,
            location: 'JF'
          },
          {
            name: 'Thego',
            surname: 'Leo',
            age: 20,
            location: 'Stone Ridge'
          },
          {
            name: 'Kopano',
            surname: 'Radi',
            age: 26,
            location: 'JF'
          },
          {
            name: 'Mahletse',
            surname: 'Radi',
            age: 35,
            location: 'Penina parke'
          }
            
        ]
      }

	render() {
		return (
			<div className="cover">
                
			<ul>
	    <h1>{ this.state.output }</h1>
            {this.state.Users.map(element =><li>{element.name +' ' + element.surname + ' ' + element.location + '  -  ' + element.age}</li>)}
      </ul>
      </div>
      );
}

}
